const initialState = {
  id: '',
  login: null,
};

export const LOGIN_ACTION_TYPE = {
  SET_ID: 'SET_ID',
  SET_LOGIN: 'SET_LOGIN',
};

export const loginReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN_ACTION_TYPE.SET_ID:
      return {
        ...state,
        id: action.payload, //   
      };
    case LOGIN_ACTION_TYPE.SET_LOGIN:
      return {
        ...state,
        login: action.payload, //   
      };
    default:
      return state; //       
  }
};
